* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

.container {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
  padding: 0 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Times New Roman", Times, serif;
  margin: 0;
  font-weight: 400;
  text-align: center;
  line-height: 1.2;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.2rem;
}

h4 {
  font-size: 1.8rem;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.section {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.mt-1 {
  margin-top: 1rem;
}

.mb-1 {
  margin-bottom: 1rem;
}

.pt-1 {
  padding-top: 1rem;
}

.pb-1 {
  padding-bottom: 1rem;
}

.focused-content {
  max-width: 80rem;
}

.white-background {
  background-color: #ffffff;
}

.dark-background {
  background-color: #000000;
  color: #ffffff;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.highlighted-white-text {
  font-weight: 900;
  color: #ffffff;
}

.highlighted-black-text {
  font-weight: 900;
  color: #000000;
}

.no-margin {
  margin: 0;
}

.black-text {
  color: #000000;
}

.large-text {
  font-size: 3rem;
}

.margin-title {
  margin: 0 0 2rem 0;
}

.button {
  background-color: #000000;
  background: rgba(18, 18, 18, 0.5);
  border-radius: 0.7rem;
  border: 1.5px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  padding: 0.75rem 1.5rem;
  color: #ffffff;
  font-size: 1.5rem;
}
.button:hover {
  font-weight: 700;
}

.header .header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.header .header-content .navigation,
.header .header-content .platform-button {
  display: flex;
  gap: 3rem;
  background-color: #000000;
  background: rgba(18, 18, 18, 0.5);
  border-radius: 0.7rem;
  border: 1.5px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  padding: 0.75rem 1.5rem;
  color: #ffffff;
}
.header .header-content .navigation a,
.header .header-content .platform-button a {
  color: #ffffff;
  font-size: 1.5rem;
}
.header .header-content .navigation a:hover,
.header .header-content .platform-button a:hover {
  font-weight: 700;
}
@media (max-width: 64em) {
  .header .header-content .navigation,
  .header .header-content .platform-button {
    display: none;
  }
}

.header-logo img {
  width: 3.86375rem;
  height: 4.375rem;
}

.menu {
  cursor: pointer;
  z-index: 4;
}
@media (min-width: 64em) {
  .menu {
    display: none;
  }
}

.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  display: none;
  background-color: #123524;
  color: #ffffff;
  flex-direction: column;
  padding: 2rem;
  z-index: 3;
}
.menu-overlay .menu-overlay-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}
.menu-overlay .menu-overlay-header .close-button {
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
}
.menu-overlay .menu-overlay-header .menu-overlay-logo {
  display: flex;
  flex-direction: row;
  text-align: left;
  color: #ffffff;
  gap: 1rem;
  align-items: center;
}
.menu-overlay .menu-overlay-header .menu-overlay-logo img {
  width: 3.86375rem;
  height: 4.375rem;
}
.menu-overlay .menu-overlay-header .menu-overlay-logo h2 {
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  text-align: left;
  font-weight: 700;
}
.menu-overlay .menu-overlay-header .menu-overlay-logo span {
  font-size: 1.2rem;
}
.menu-overlay .menu-overlay-navigation ul {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.menu-overlay .menu-overlay-navigation ul li {
  list-style: none;
}
.menu-overlay .menu-overlay-navigation ul li a {
  color: #ffffff;
}

.header__hero {
  height: 100vh;
}
.header__hero .header__video,
.header__hero .header__image {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.header__hero .overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0), rgb(0, 0, 0));
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__hero .overlay .overlay-text {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  color: #ffffff;
}
.header__hero .overlay .overlay-text h1 {
  font-size: 5rem;
  display: flex;
  flex-direction: column;
}
@media (max-width: 30em) {
  .header__hero .overlay .overlay-text h1 {
    font-size: 4rem;
  }
}
.header__hero .overlay .overlay-widgets {
  flex: 1;
  padding: 4rem;
  position: relative;
}
.header__hero .overlay .overlay-widgets .widget-last-new {
  display: grid;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  gap: 1rem;
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  background-color: rgba(0, 0, 0, 0.6);
  color: #ffffff;
  padding: 2rem;
  border-radius: 0.7rem;
  max-width: 40rem;
  font-size: 1.2rem;
}
@media (max-width: 30em) {
  .header__hero .overlay .overlay-widgets .widget-last-new {
    grid-template-columns: 1fr;
    bottom: 0.5rem;
    right: 0.5rem;
    left: 0.5rem;
    width: calc(100% - 1rem);
    justify-items: center;
    text-align: center;
    padding: 1rem;
    font-size: 1rem;
  }
  .header__hero .overlay .overlay-widgets .widget-last-new img {
    display: none;
  }
}
.header__hero .overlay .overlay-widgets .widget-last-new .widget-last-new-alert {
  grid-column: 1/3;
}
.header__hero .overlay .overlay-widgets .widget-last-new .widget-last-new-alert .red-point {
  display: inline-block;
  width: 0.625rem;
  height: 0.625rem;
  background-color: #FF0000;
  border-radius: 50%;
  margin-right: 0.5rem;
  animation: parpadeo 2s infinite;
}
.header__hero .overlay .overlay-widgets .widget-last-new .widget-last-new-text h3 {
  margin-bottom: 1rem;
}

.music-button {
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  border-radius: 50%;
  padding: 1rem;
  cursor: pointer;
  position: fixed;
  bottom: 1rem;
  left: 1rem;
  width: 4rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  transition: background-color 0.3s;
}
.music-button.active {
  background-color: #123524;
}

@keyframes parpadeo {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.footer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #123524;
  color: #ffffff;
}
.footer .footer-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  gap: 3rem;
}
.footer .footer-sitemap {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 3rem;
}
.footer .footer-sitemap .footer-column h3 {
  font-size: 1.6rem;
  margin-bottom: 2rem;
  text-align: left;
}
.footer .footer-sitemap .footer-column ul {
  list-style: none;
}
@media (max-width: 30em) {
  .footer .footer-sitemap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer .footer-inferior {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 2rem;
}
.footer .footer-inferior .media {
  display: flex;
  gap: 3rem;
}
.footer .footer-inferior .media a {
  color: #ffffff;
}
@media (max-width: 48em) {
  .footer .footer-inferior {
    display: flex;
    flex-direction: column;
    gap: 3rem;
  }
}

.footer-logo {
  display: inline-flex;
  align-items: flex-start;
  gap: 1rem;
  color: #ffffff;
  text-align: left;
}
.footer-logo img {
  width: 3.86375rem;
  height: 4.375rem;
}
.footer-logo h2 {
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  text-align: left;
  font-weight: 700;
}
.footer-logo span {
  font-size: 1.2rem;
}

.who-we-are-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.who-we-are-section .who-we-are-section--text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.who-we-are-section .who-we-are-section--text p span {
  display: block;
}
@media (max-width: 48em) {
  .who-we-are-section {
    grid-template-columns: repeat(1, 1fr);
    gap: 3rem;
  }
  .who-we-are-section p {
    font-size: 2rem;
  }
}

.services-section {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.services-section .services-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5rem;
}
@media (max-width: 48em) {
  .services-section .services-list {
    grid-template-columns: repeat(1, 1fr);
  }
}
.services-section .services-list .services-item {
  background-color: #123524;
  color: #ffffff;
}
.services-section .services-list .services-item .services-item-text {
  padding: 4rem;
}

.news-section {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 3rem;
  color: #000000;
}
@media (max-width: 48em) {
  .news-section .news-item--featured {
    padding-bottom: 2rem;
    border-bottom: 1px solid #e1e1e1;
  }
}
.news-section .news-item--secondary {
  display: flex;
  flex-direction: column;
}
.news-section .news-item--secondary a {
  padding-top: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #e1e1e1;
  color: #000000;
}
.news-section .news-item--secondary a:first-of-type {
  padding-top: 0;
}
.news-section .news-item--secondary .news-item--small {
  display: grid;
  grid-template-columns: 3fr 7fr;
  gap: 2rem;
}
@media (max-width: 30em) {
  .news-section .news-item--secondary .news-item--small .news-item__excerpt {
    display: none;
  }
}
@media (max-width: 48em) {
  .news-section {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #e1e1e1;
  }
}

.post-section {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  max-width: 80rem;
  margin: 0 auto;
}
@media (max-width: 48em) {
  .post-section {
    max-width: 100%;
  }
}
.post-section .post-details {
  display: flex;
  flex-direction: row;
  gap: 3rem;
}

.goal-section p span {
  display: block;
}

.about-us-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}

.facts-section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
@media (max-width: 30em) {
  .facts-section {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}
.facts-section .facts-item {
  position: relative;
  border: 0.1px solid #e1e1e1;
  padding: 2rem;
  color: #123524;
}
.facts-section .facts-item .facts-text {
  position: absolute;
  top: 1rem;
  left: 2rem;
  margin: 0;
}
.facts-section .facts-item .facts-number {
  font-size: 5rem;
  margin: 10rem 0 5rem 0;
}

.mission-vission-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}
@media (max-width: 48em) {
  .mission-vission-section {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 64em) {
  .mission-vission-section img {
    grid-column: 1/2;
  }
}
.mission-vission-section .mission-vission-section-text {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 64em) {
  .mission-vission-section .mission-vission-section-text {
    grid-column: 2/4;
    grid-row: 1/2;
  }
}

@media (min-width: 64em) {
  .ceo-message-section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10rem;
  }
}
.ceo-message-section .ceo-message--text {
  margin: auto;
}
@media (max-width: 48em) {
  .ceo-message-section .ceo-message--text {
    margin-bottom: 3rem;
  }
}
.ceo-message-section .ceo-message--text blockquote {
  padding-left: 5rem;
  position: relative;
  margin-bottom: 3rem;
}
.ceo-message-section .ceo-message--text blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}

.team-section .team-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 3rem;
}
@media (max-width: 48em) {
  .team-section .team-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.news-last {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.news-last img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.news-last .news-last-content {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0), rgb(0, 0, 0));
  color: #ffffff;
}
.news-last .news-last-content h2 {
  margin-bottom: 1rem;
}
.news-last .news-last-content p {
  color: #ffffff;
}

.news-secondary {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 3rem;
  border-bottom: 1px solid #e1e1e1;
}
.news-secondary:last-of-type {
  border-bottom: none;
}
.news-secondary .news-secondary-content p {
  color: #000000;
}

.info-estancia {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
  background-color: #000000;
  color: #ffffff;
}

.rooms-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
