.contenedor-principal {
  margin-top: 200px;
}

/* Título centrado */
.actividades-h2 {
  text-align: center;
  margin-bottom: 30px;
}

/* Carrusel centrado */
.carrusel {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

/* Tarjeta de actividad */
.actividad {
  flex: 0 0 280px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.actividad:hover {
  transform: scale(1.02);
}

.actividad img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.actividad h3 {
  font-size: 1.1rem;
  margin: 12px;
}

.actividad button {
  margin: 10px 12px 15px;
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s;
}

.actividad button:hover {
  background-color: #0056b3;
}

/* Modal centrado */
.actividades-modal {
  display: none;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

/* Contenido del modal */
.actividades-modal-content {
  background: white;
  padding: 25px 30px;
  border-radius: 12px;
  max-width: 450px;
  width: 90%;
  text-align: center;
  position: relative;
  animation: slideIn 0.3s ease;
}

/* Botón de cierre */
.close {
  position: absolute;
  right: 15px;
  top: 10px;
  font-size: 26px;
  font-weight: bold;
  color: #888;
  cursor: pointer;
  transition: color 0.3s;
}

.close:hover {
  color: #000;
}

/* Animación modal */
@keyframes slideIn {
  from {
    transform: translateY(-30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.modal-dialog {
  margin-top: 100px; /* Ajusta el valor según lo que necesites */
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: url("/build/img/fondo.jpg") center center/cover no-repeat;
  filter: blur(5px) brightness(0.5);
  z-index: -1;
  animation: moveBackground 20s linear infinite alternate;
}

/* Contenedor principal */
.contenedor {
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 40px;
  padding-left: 50px;
  padding-right: 50px;
  color: white;
}

/* Formulario */
.formulario {
  width: 45%;
  background: rgba(0, 0, 0, 0.5);
  padding: 30px;
  border-radius: 10px;
  animation: fadeIn 1s ease;
}

.formulario h2 {
  margin-bottom: 20px;
  text-align: center;
  font-size: 28px;
  color: #f0f0f0;
  letter-spacing: 1px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  animation: glow 2s infinite alternate;
}

.formulario input,
.formulario textarea,
.formulario button {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  transition: all 0.4s ease;
  position: relative;
  z-index: 1;
}

.formulario input:focus,
.formulario textarea:focus {
  border-color: #FFDD57;
  outline: none;
  box-shadow: 0 0 10px #FFDD57, 0 0 20px #FFDD57;
  background: linear-gradient(135deg, rgba(255, 221, 87, 0.1), rgba(255, 221, 87, 0.2));
}

/* Efecto hover brillante en botón */
.formulario button {
  background-color: #20b954;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  transition: all 0.4s ease;
  box-shadow: 0 0 0 transparent;
}

.formulario button:hover {
  background-color: #25D366;
  transform: scale(1.05);
  box-shadow: 0 0 10px #25D366, 0 0 20px #25D366;
}

/* Información lateral */
.informacion {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.4);
  padding: 30px;
  border-radius: 10px;
  animation: fadeIn 1s ease;
}

.direccion {
  margin-bottom: 30px;
}

.direccion h3 {
  font-size: 22px;
  margin-bottom: 10px;
  color: #ffdd57;
  text-transform: uppercase;
  letter-spacing: 1px;
  animation: slideIn 1s ease, glow 2s infinite alternate;
}

.direccion p {
  margin: 5px 0;
  line-height: 1.4;
  color: #f0f0f0;
  text-align: justify;
  animation: fadeIn 1s ease;
}

/* Redes sociales */
.redes-sociales {
  text-align: center;
  margin-top: 30px;
}

.redes-sociales h3 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #ffdd57;
  animation: slideIn 1s ease, glow 2s infinite alternate;
}

.redes-sociales .iconos {
  display: flex;
  justify-content: center;
  gap: 30px;
}

.redes-sociales .iconos a img {
  width: 32px;
  height: auto;
  filter: brightness(0.8);
  transition: all 0.4s ease;
}

.redes-sociales .iconos a img:hover {
  transform: scale(1.2);
  filter: brightness(1.2);
  box-shadow: 0 0 10px #fff, 0 0 20px #ffdd57;
}

/* Botón de WhatsApp */
.whatsapp-link {
  text-decoration: none;
}

.whatsapp-button {
  background-color: #25D366;
  color: white;
  margin: 0 auto;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.4s ease;
  box-shadow: 0 0 0 transparent;
}

.whatsapp-button:hover {
  background-color: #20b954;
  transform: scale(1.05);
  box-shadow: 0 0 10px #20b954, 0 0 20px #20b954;
}

.whatsapp-button img {
  width: 24px;
  height: 24px;
}

/* Animaciones */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes glow {
  0% {
    text-shadow: 0 0 5px #FFDD57, 0 0 10px #FFDD57;
  }
  100% {
    text-shadow: 0 0 15px #FFDD57, 0 0 25px #FFDD57;
  }
}
@keyframes moveBackground {
  0% {
    background-position: center top;
  }
  100% {
    background-position: center bottom;
  }
}
